.form {

  &__group {
    &:not(:last-child) {
      margin-bottom: 2rem;
    }
  }

  /**
   * input elements don't inherit font-family
   */
  &__input {
    font-size: 1.5rem;
    font-family: inherit;
    color: inherit;
    padding: 1.5rem 2rem;
    border-radius: .2rem;
    background-color: rgba($color-white, .5);
    border: none;
    border-bottom: .3rem solid transparent;
    width: 90%;
    transition: all .3s;

    @include respond(tab-port) {
        width: 100%;
    }

    &:focus {
      outline: none;
      box-shadow: 0 1rem 2rem rgba($color-black, .1);
      border-bottom: .3rem solid $color-primary;
    }
    &:focus:invalid {
      border-bottom: .3rem solid $color-secondary-dark;
    }
    &::-webkit-input-placeholder {
      color: $color-grey-dark-2;
    }
  }


  &__label {
    font-size: 1.2rem;
    font-weight: 700;
    margin-left: 2rem;
    margin-top: .7rem;
    display: block;
    transition: all .2s;
  }

  /*sibling selector*/
  &__input:placeholder-shown + &__label {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-4rem);
  };

  &__radio-group {
    width: 40%;
    display: inline-block;

    @include respond(tab-port) {
        width: 100%;
        margin-bottom: 2rem;
    }
  }
  &__radio-input {
    display: none;
  }
  &__radio-label {
    font-size: $default-font-size;
    cursor: pointer;
    position: relative;
    padding-left: 5rem;
  }
  &__radio-button {
    height: 3rem;
    width: 3rem;
    border: .5rem solid $color-primary;
    border-radius: 50%;
    display: inline-block;
    position: absolute;
    top: -.8rem;
    left: 0;

    &::after {
      content: "";
      height: 1.3rem;
      width: 1.3rem;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: $color-primary;
      opacity: 0;
    }
  }

  /*general sibling selector (adjacet sibling is also  general sibling)*/
  &__radio-input:checked ~ &__radio-label &__radio-button::after {
    opacity: 1;
  }
}
